.button {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    width: 132px;
    height: 30px;

}

.disabled {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;
    background: #004D99;
    opacity: 0.5;
    border-radius: 3px;

}

.secondary {
    width: 160px;
    height: 30px;
    color: #0066CC;
    background: #ffffff;
    border: 1px solid #0066CC;
    border-radius: 3px;
}

.default {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10px 50px;
    gap: 10px;
    background: #0066CC;
    border-radius: 3px;
    color: #FFFFFF;

}

.red {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;
    background: #CC1439;
    border-radius: 3px;
    color: #FFFFFF;
}

.default:hover {
    cursor: pointer;
    background: rgba(0, 128, 255, 1);

}

.red:hover {
    cursor: pointer;
    background: rgba(255, 26, 71, 1);


}

.secondary:hover {
    color: #0066CC;
    cursor: pointer;
    background: rgba(229, 240, 250, 1)

}


.default:active {
    background: rgba(0, 89, 178, 1);

}

.secondary:active {
    border: 1px solid rgba(0, 102, 204, 1);
    background: rgba(229, 240, 250, 1);
    color: #0066CC;
}

.red:active {
    background: rgba(178, 18, 50, 1);

}

.button:disabled {
    opacity: 50%;
}
